<template>
    <div class="init-screen">
        <div v-show="showHeader" class="os-header">
            <div>OpenOS 1.6.8 (2048k RAM)</div>
            <div>Welcome to the dark side - here, have some cookies.</div>
            <div>Have you tried turning it off and on again?</div>
        </div>
        <div v-show="showInputLine" class="os-input-line">
            <span style="color: red">/home #&nbsp;</span>
            <span style="color: white" v-show="showCursor">█</span>
        </div>
        <div v-for="(line, index) in displayedLines" v-bind:key="index">{{ line }}</div>
    </div>
</template>

<script>
    // eslint-disable-next-line no-unused-vars
    let intervalID;

    const loadLines = [
        "Booting OpenOS 1.6.8...",
        "Initializing package management...",
        "/lib/package.lua",
        "/lib/buffer.lua",
        "/lib/filesystem.lua",
        "/lib/io.lua",
        "Initializing file system...",
        "Running boot scripts...",
        "boot/00_base.lua",
        "boot/01_process.lua",
        "boot/02_os.lua",
        "boot/03_io.lua",
        "boot/04_component.lua",
        "boot/10_devfs.lua",
        "boot/90_filesystem.lua",
        "boot/91_gpu.lua",
        "boot/92_keyboard.lua",
        "boot/93_term.lua",
        "boot/94_shell.lua",
        "boot/99_rc.lua",
        "Initializing components...",
        "Initializing system...",
    ];

    async function sleep(time) {
        await new Promise(resolve => setTimeout(resolve, time))
    }

    export default {
        name: "InitScreen",
        data: function () {
            return {
                displayedLines: [],
                showHeader: false,
                showInputLine: false,
                showCursor: true
            }
        },

        async created() {
            for (const line in loadLines) {
                await sleep(Math.floor(Math.random() * 150));
                this.displayedLines.push(loadLines[line])
            }
            await sleep(400);
            this.displayedLines = [];
            this.showHeader = true;
            await sleep(1000);
            this.showInputLine = true;
            intervalID = setInterval(() => {
                this.showCursor = !this.showCursor
            }, 500);
        },

        destroyed() {
            clearInterval(intervalID)
        }
    }

</script>

<style scoped>
    .init-screen {
        position: absolute;
        padding: 2ch;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: black;
        color: white;
        font-size: large;
        font-family: monospace;
    }

    .os-header {
        border: 0.25ch solid white;
        padding: 1ch;
        width: 80ch;
    }

    .os-input-line {
        margin-top: 1ch;
    }
</style>